<style lang="scss" scoped>
.avatar {
  left: 1px;
  top: 5px;
  width: 35px;
}

.team-member {
  padding-left: 44px;

  .avatar {
    top: 2px;
  }
}

.ball-pulse {
  right: 8px;
  top: 37px;
  position: absolute;
}
</style>

<template>
  <div>
    <div v-show="updatedTeam.parsed_description && !editMode" class="bb bb-gray">
      <div class="lh-copy ma0 pl3 pt3 pr3 parsed-content" :class="{ mb3: !teamMemberOrAtLeastHR() }" v-html="updatedTeam.parsed_description">
      </div>

      <p v-if="teamMemberOrAtLeastHR()" class="pl3 pb3 pr3 f6 mb0">
        <a class="bb b--dotted bt-0 bl-0 br-0 pointer" data-cy="team-description-edit" @click.prevent="editMode = true">{{ $t('app.edit') }}</a>
      </p>
    </div>

    <!-- team description blank -->
    <div v-show="!updatedTeam.parsed_description && !editMode" class="lh-copy ma0 pa3 bb bb-gray">
      <a v-if="teamMemberOrAtLeastHR()" class="bb b--dotted bt-0 bl-0 br-0 pointer" @click.prevent="editMode = true" data-cy="add-description-blank-state">{{ $t('team.description_cta') }}</a>

      <span v-if="!teamMemberOrAtLeastHR()" class="f6">
        {{ $t('team.description_blank') }}
      </span>
    </div>

    <!-- form to add the description -->
    <div v-show="editMode" class="pa3 bb bb-gray">
      <form @submit.prevent="submit">
        <template v-if="form.errors.length > 0">
          <div class="cf pb1 w-100 mb2">
            <errors :errors="form.errors" />
          </div>
        </template>

        <text-area v-model="form.description"
                   :label="$t('team.description_title')"
                   :datacy="'team-description-textarea'"
                   :required="true"
                   :rows="10"
                   :help="$t('team.description_help')"
        />

        <div class="mb0">
          <div class="flex-ns justify-between">
            <div>
              <a class="btn btn-secondary dib tc w-auto-ns w-100 mb2 pv2 ph3" @click.prevent="editMode = false">
                {{ $t('app.cancel') }}
              </a>
            </div>
            <loading-button :classes="'btn add w-auto-ns w-100 mb2 pv2 ph3'" :state="loadingState" :text="$t('app.publish')" :cypress-selector="'team-description-submit-description-button'" />
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import TextArea from '@/Shared/TextArea';
import LoadingButton from '@/Shared/LoadingButton';
import Errors from '@/Shared/Errors';

export default {
  components: {
    TextArea,
    LoadingButton,
    Errors,
  },

  props: {
    team: {
      type: Object,
      default: null,
    },
    userBelongsToTheTeam: {
      type: Boolean,
      default: false,
    }
  },

  data() {
    return {
      editMode: false,
      form: {
        description: null,
        errors: [],
      },
      loadingState: '',
      updatedTeam: null,
    };
  },

  created: function() {
    this.updatedTeam = this.team;
    this.form.description = this.team.raw_description;
  },

  methods: {
    submit() {
      this.loadingState = 'loading';

      axios.post('/' + this.$page.auth.company.id + '/teams/' + this.team.id + '/description', this.form)
        .then(response => {
          this.$snotify.success(this.$t('employee.description_success'), {
            timeout: 2000,
            showProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
          });

          this.updatedTeam = response.data.data;
          this.form.description = this.updatedTeam.raw_description;
          this.editMode = false;
          this.loadingState = null;
        })
        .catch(error => {
          this.loadingState = null;
          this.form.errors = _.flatten(_.toArray(error.response.data));
        });
    },

    teamMemberOrAtLeastHR() {
      if (this.$page.auth.employee.permission_level <= 200) {
        return true;
      }

      if (this.userBelongsToTheTeam == false) {
        return false;
      }

      return true;
    }
  }
};

</script>
